import React, { useState, useEffect } from "react";
import axios from "axios";
import "./character.css";

export default function Character() {
  const [filter, setFilter] = useState(0);
  const [items, setItems] = useState([]);

  useEffect(() => {
    // const params = { email: "alextest@gmail.com" };
    const params = { email: localStorage.email };

    axios
      .post(process.env.REACT_APP_API_URL + "getCharacters", params)
      .then((res) => {
        if (res.status === 200) {
          setItems(res.data.characters);
        } else {
          console.log(res.data.msg);
        }
      })
      .catch((err) => {
        if (
          err.response.status === 401 ||
          err.response.status === 404 ||
          err.response.status === 403 ||
          err.response.status === 400
        ) {
          console.log(err.response.data.msg);
        }
        console.log("login data submit error: ", err);
      });
  }, []);

  const classTexts = {
    0: "Warrior",
    1: "Ranger",
    2: "Marge",
    3: "Rouge",
    4: "Necromencer",
    5: "Druid",
    6: "Cleric",
    7: "Wizard",
    8: "Paladin",
    9: "Magician",
    10: "Shadow Knight",
    11: "Bard",
    12: "Sharman",
  };
  const raceTexts = { 0: "Dark Elf", 1: "Dwarf", 2: "Halfling", 3: "Human" };
  const genderTexts = { 0: "Male", 1: "Female" };

  return (
    <div className="character-items">
      <div className="filter">
        <p>Filter</p>
        <p className="separator"></p>
        <p
          className={`${filter === 0 ? "active" : ""}`}
          onClick={() => setFilter(0)}
        >
          All
        </p>
        <p
          className={`${filter === 1 ? "active" : ""}`}
          onClick={() => setFilter(1)}
        >
          Gender
        </p>
        <p
          className={`${filter === 2 ? "active" : ""}`}
          onClick={() => setFilter(2)}
        >
          Class
        </p>
        <p
          className={`${filter === 3 ? "active" : ""}`}
          onClick={() => setFilter(3)}
        >
          Race
        </p>
      </div>
      <div className="list">
        {items.map((item, index) => (
          <div className="item" key={index}>
            <img
              src="https://static.wikia.nocookie.net/characterprofile/images/0/08/KratosGreekEra.png"
              alt="item"
            />
            <div className="values">
              <p>Race: {raceTexts[item.race]}</p>
              <p>Gender: {genderTexts[item.gender]}</p>
              <p>Classe: {classTexts[item.classe]}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
