import React, { useState } from "react";
import { Mainnet, useEthers, Goerli } from "@usedapp/core";
import { getDefaultProvider } from "ethers";
import Navbar from "../Navbar/Navbar";
import Character from "./character/character";
import NFTInfo from "./nftinfo/nftinfo";
import Land from "./landpurchase/landpurchase";
import Item from "./item/item";
import "./dashboard.css";

export default function Dashboard() {
  const { account, deactivate, activateBrowserWallet, chainId } = useEthers();

  const [route, setRoute] = useState("character");

  const config = {
    readOnlyChainId: Mainnet.chainId,
    readOnlyUrls: {
      [Mainnet.chainId]: getDefaultProvider("mainnet"),
      [Goerli.chainId]: getDefaultProvider("goerli"),
    },
  };
  if (!config.readOnlyUrls[chainId]) {
    return <p>Please use either Mainnet or Goerli testnet.</p>;
  }

  const onRouteChange = (event) => {
    setRoute(event);
  };

  if (
    localStorage.isAuthenticated &&
    JSON.parse(localStorage.isAuthenticated)
  ) {
    console.log("login first");
  } else {
    window.location.href = "/login";
  }

  return (
    <div className="dashboard">
      <Navbar onRouteChange={onRouteChange} connect={activateBrowserWallet} />

      {account && (
        <button
          style={{
            position: "absolute",
            color: "black",
            right: 20,
            margin: "5px",
          }}
          onClick={() => deactivate()}
        >
          Disconnect
        </button>
      )}
      {!account ? (
        <p
          style={{
            marginTop: "150px",
            textAlign: "center",
            color: "white",
            fontSize: "45px",
          }}
        >
          {" "}
          Please Connect Meta Mask
        </p>
      ) : (
        <>
          <div className="profile-container">
            <div className="profile">
              <img
                src="https://unrealkingdoms.com/img/unreal.png"
                alt="cover"
              />
              <img
                src="https://www.shareicon.net/data/2015/06/01/47332_man_512x512.png"
                alt="avatar"
              />
              <p>{localStorage.email}</p>
            </div>
          </div>

          {route === "character" ? (
            <Character />
          ) : route === "nft" ? (
            <NFTInfo />
          ) : route === "item" ? (
            <Item />
          ) : (
            <Land />
          )}
        </>
      )}
    </div>
  );
}
