import React, { useState, useEffect } from "react";
import axios from "axios";

import "./landpurchase.css";

export default function Land() {
  const [filter, setFilter] = useState(0);
  const [items, setItems] = useState([]);

  useEffect(() => {
    // const params = { email: "dmtris.alex7@gmail.com" };
    const params = { email: localStorage.email };

    axios
      .post(process.env.REACT_APP_API_URL + "getLands", params)
      .then((res) => {
        if (res.status === 200) {
          setItems(res.data.lands);
        } else {
          console.log(res.data.msg);
        }
      })
      .catch((err) => {
        if (
          err.response.status === 401 ||
          err.response.status === 404 ||
          err.response.status === 403 ||
          err.response.status === 400
        ) {
          console.log(err.response.data.msg);
        }
        console.log("login data submit error: ", err);
      });
  }, []);

  const plotsTexts = {
    0: "Forest Plot",
    1: "Ice Plot",
    2: "Nether Plot",
    3: "Volcanic Plot",
    4: "Sand Plots",
  };
  const sizeTexts = {
    0: "64 - 64",
    1: "128 - 128",
    2: "256 - 256",
  };
  const typeTexts = {
    0: "Gold",
    1: "Platnium",
    2: "Diamond",
  };
  const imageTexts = {
    0: "https://unrealkingdoms.com/static/media/forest_mint.0a7a163a.png",
    1: "https://unrealkingdoms.com/static/media/ice_mint.a033e4e2.png",
    2: "https://unrealkingdoms.com/static/media/nether_mint.a31dba6a.png",
    3: "https://unrealkingdoms.com/static/media/volcano_mint.088ec3de.png",
    4: "https://unrealkingdoms.com/static/media/sand_mint.a181249d.png",
  };
  return (
    <div className="land-items">
      <div className="filter">
        <p>Filter</p>
        <p className="separator"></p>
        <p
          className={`${filter === 0 ? "active" : ""}`}
          onClick={() => setFilter(0)}
        >
          All
        </p>
        <p
          className={`${filter === 1 ? "active" : ""}`}
          onClick={() => setFilter(1)}
        >
          Gold
        </p>
        <p
          className={`${filter === 2 ? "active" : ""}`}
          onClick={() => setFilter(2)}
        >
          Platinum
        </p>
        <p
          className={`${filter === 3 ? "active" : ""}`}
          onClick={() => setFilter(3)}
        >
          Diamond
        </p>
      </div>
      <div className="list">
        {items.map((item, index) => (
          <div className="item" key={index}>
            <img src={imageTexts[item.plots]} alt="item" />
            <div className="values">
              <p>Plots: {plotsTexts[item.plots]}</p>
              <p>Size: {sizeTexts[item.size]}</p>
              <p>Type: {typeTexts[item.landtype]}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
