import React, { useState, useEffect } from "react";
import axios from "axios";

import "./item.css";

export default function Item() {
  const [filter, setFilter] = useState(0);
  const [items, setItems] = useState([]);

  useEffect(() => {
    // const params = { email: "dmtris.alex7@gmail.com" };
    const params = { email: localStorage.email };

    axios
      .post(process.env.REACT_APP_API_URL + "getItems", params)
      .then((res) => {
        if (res.status === 200) {
          setItems(res.data.items);
        } else {
          console.log(res.data.msg);
        }
      })
      .catch((err) => {
        if (
          err.response.status === 401 ||
          err.response.status === 404 ||
          err.response.status === 403 ||
          err.response.status === 400
        ) {
          console.log(err.response.data.msg);
        }
        console.log("data submit error: ", err);
      });
  }, []);

  const itemText = {
    0: "Axe",
    1: "Axe2",
    2: "Short Sword",
    3: "Spear",
    4: "Shield",
    5: "Hammer",
    6: "Long Sword",
    7: "Bow",
    8: "Dagger",
    9: "Omega Sword",
    10: "Rusty Dagger",
    11: "Rusty Sword",
    12: "Wood Staff",
    13: "Filmsy Bow",
    14: "Wom Shield",
    15: "Rust Mace",
    16: "Ripped Book",
    17: "Rusty Great Sword",
  };

  const typeText = {
    0: "Primary Weapon",
    1: "Secondary Weapon",
  };

  const specificText = {
    0: "Aex",
    1: "Sword",
    2: "Spear",
    3: "Shield",
    4: "Hammer",
    5: "Box",
    6: "Dagger",
    7: "Staff",
    8: "Other",
  };

  const equipmentText = {
    0: "One Handed",
    1: "Two Handed",
  };

  return (
    <div className="wp-items">
      <div className="filter">
        <p>Filter</p>
        <p className="separator"></p>
        <p
          className={`${filter === 0 ? "active" : ""}`}
          onClick={() => setFilter(0)}
        >
          All
        </p>
        <p
          className={`${filter === 1 ? "active" : ""}`}
          onClick={() => setFilter(1)}
        >
          Item
        </p>
        <p
          className={`${filter === 2 ? "active" : ""}`}
          onClick={() => setFilter(2)}
        >
          Type
        </p>
        <p
          className={`${filter === 3 ? "active" : ""}`}
          onClick={() => setFilter(3)}
        >
          Specific Type
        </p>
        <p
          className={`${filter === 4 ? "active" : ""}`}
          onClick={() => setFilter(4)}
        >
          Equipment Type
        </p>
      </div>
      <div className="list">
        {items.map((item, index) => (
          <div className="item" key={index}>
            <img
              src="https://www.pngall.com/wp-content/uploads/2016/04/Backpack-PNG-Pic.png"
              alt="item"
            />
            <div className="values">
              <p>Name: {itemText[item.itemid]}</p>
              <p>Type: {typeText[item.itemtype]}</p>
              <p>Specific Type: {specificText[item.specifictype]}</p>
              <p>Equipment: {equipmentText[item.equipmenttype]}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
