import React from "react";
import { useEthers } from "@usedapp/core";
import {
  Nav,
  NavLink,
  Logo,
  Bars,
  NavMenu,
  NavBtn,
  NavBtnLink,
} from "./NavbarElement";

const Navbar = (props) => {
  const { account } = useEthers();

  const handleLogout = () => {
    localStorage.isAuthenticated = false;
    window.location.href = "/";
  };

  return (
    <Nav>
      <Bars />
      <Logo
        src="https://unrealkingdoms.com/static/media/logo.bf60290e.png"
        alt="logo"
        onClick={handleLogout}
      />
      <NavMenu>
        <NavLink
          onClick={() => {
            props.onRouteChange("character");
          }}
        >
          Character
        </NavLink>
        <NavLink
          onClick={() => {
            props.onRouteChange("item");
          }}
        >
          Item
        </NavLink>
        <NavLink
          onClick={() => {
            props.onRouteChange("nft");
          }}
        >
          NFT Information
        </NavLink>
        <NavLink
          onClick={() => {
            props.onRouteChange("land");
          }}
        >
          Land Purchase Location
        </NavLink>
      </NavMenu>
      <NavBtn>
        <NavBtnLink
          onClick={() => {
            props.connect();
          }}
        >
          {account && account.length > 0 ? account : "Connect"}
        </NavBtnLink>
      </NavBtn>
    </Nav>
  );
};

export default Navbar;
