import React, { useState, useEffect } from "react";
import { useEthers } from "@usedapp/core";
import axios from "axios";

import "./nftinfo.css";

export default function Character() {
  const [filter, setFilter] = useState(0);
  const [items, setItems] = useState([]);
  const { account } = useEthers();

  useEffect(() => {
    // const params = { address: "0x27Aa9F1F479ECe342210965CfA92133D5B5308A6" };
    const params = { address: account };

    axios
      .post(process.env.REACT_APP_API_URL + "getallnfts", params)
      .then((res) => {
        if (res.status === 200) {
          setItems(res.data.result);
        } else {
          console.log(res.data.msg);
        }
      })
      .catch((err) => {
        if (
          err.response.status === 401 ||
          err.response.status === 404 ||
          err.response.status === 403 ||
          err.response.status === 400
        ) {
          console.log(err.response.data.msg);
        }
        console.log("login data submit error: ", err);
      });
  }, []);

  return (
    <div className="nft-items">
      <div className="filter">
        <p>Filter</p>
        <p className="separator"></p>
        <p
          className={`${filter === 0 ? "active" : ""}`}
          onClick={() => setFilter(0)}
        >
          All
        </p>
        <p
          className={`${filter === 1 ? "active" : ""}`}
          onClick={() => setFilter(1)}
        >
          Symbol
        </p>
        <p
          className={`${filter === 2 ? "active" : ""}`}
          onClick={() => setFilter(2)}
        >
          Name
        </p>
        <p
          className={`${filter === 3 ? "active" : ""}`}
          onClick={() => setFilter(3)}
        >
          Token Address
        </p>
        <p
          className={`${filter === 4 ? "active" : ""}`}
          onClick={() => setFilter(4)}
        >
          Amount
        </p>
      </div>
      <div className="list">
        {items.map((item, index) => (
          <div className="item" key={index}>
            <img
              src="https://images.squarespace-cdn.com/content/v1/58d09402db29d660e4781a57/1619307661156-633A64D5QIIDUAGOUPEJ/culture_cryptocurrency.png"
              alt="item"
            />
            <div className="values">
              <p>Symbol: {item.symbol}</p>
              <p>Name: {item.name}</p>
              <p>Token Address: {item.token_address}</p>
              <p>Amount: {item.amount}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
